.item {
  margin: 0;
  padding: 0.5rem 1rem;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 80vw;
  height: auto;
  cursor: pointer;
  border-radius: 40px;
}

.element {
  width: 10vw;
}

.item:hover {
  filter: drop-shadow(0 0 3rem rgba(3, 120, 189, 0.486));
  background-color: rgba(92, 92, 92, 0.356);
}

.preview {
  width: 100px;
  height: 100px;
  border-radius: 50%;
}

.ratingCont {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 70px;
}

.rating,
.add,
.rotating {
  width: 30px;
}

.add:hover {
  transform: scale(1.2);
}

.rotating {
  animation-name: rotation;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: linear;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  50% {
    transform: rotate(180deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@media (max-width: 1600px) {
  .item {
    width: 95vw;
  }
  .preview {
    width: 60px;
    height: 60px;
  }
}

@media (max-width: 900px) {
  .ratingCont {
    width: 60px;
  }
  .item {
    padding: 0.2rem 0.5rem;
  }
  .item h2 {
    font-size: 1rem;
  }
}

@media (max-width: 600px) {
  .ratingCont {
    width: 40px;
  }
  .rating,
  .add {
    width: 20px;
  }
  .item {
    width: 100vw;
    padding: 0.2rem 0.3rem;
  }
  .item h2 {
    font-size: 0.7rem;
  }
  .preview {
    width: 40px;
    height: 40px;
  }
}
